import React from "react";
import logo from './images/watermelon-logo.jpg'
import { Link, animateScroll as scroll } from "react-scroll";


class Navbar extends React.Component {
    scrollToTop = () => {
      scroll.scrollToTop();
    };
  
    render() {
      return (
        <nav className="nav" id="navbar">
          <div className="nav-content">
            <img
              src={logo}
              className="nav-logo"
              alt="Logo"
              onClick={this.scrollToTop}
            />
            <ul className="nav-items">
              <li className="nav-item">
                <Link
                  activeClass="active"
                  to="section1"
                  spy={true}
                  smooth={true}
                  offset={-60}
                  duration={500}
                >
                  Home
                </Link>
              </li>
              <li className="nav-item">
                <Link
                  activeClass="active"
                  to="section2"
                  spy={true}
                  smooth={true}
                  offset={-60}
                  duration={500}
                >
                  About
                </Link>
              </li>
              <li className="nav-item">
                <Link
                  activeClass="active"
                  to="section3"
                  spy={true}
                  smooth={true}
                  offset={-60}
                  duration={500}
                >
                  Projects
                </Link>
              </li>
              <li className="nav-item">
                <Link
                  activeClass="active"
                  to="section4"
                  spy={true}
                  smooth={true}
                  offset={-60}
                  duration={500}
                >
                  Hobbies
                </Link>
              </li>
              <li className="nav-item">
                <Link
                  activeClass="active"
                  to="section5"
                  spy={true}
                  smooth={true}
                  offset={-60}
                  duration={500}
                >
                  Research
                </Link>
              </li>
              <li className="nav-item">
                  {/* ADD DOWNLOAD BOX */}
                  CV
                  
                  </li>
            </ul>
          </div>
        </nav>
      );
    }
  }

export default Navbar;