import React from "react";

class Section extends React.Component {
  constructor(props) {
    super(props);
    this.title = props.title;
    this.content = props.content;
    this.dark = props.dark;
    this.id = props.id;
  }

  render() {
    return (
      <div className={"section" + (this.dark ? " section-dark" : "")}>
        <div className="section-content" id={this.id}>
          <h1 style={{"textAlign":"center"}}>{this.title}</h1>
          <p>{this.content}</p>
        </div>
      </div>
    );
  }
}

export default Section;