import React from "react";
import ItemBox from './ItemBox.js'

class TechnicalProjects extends React.Component {
  constructor(props) {
    super(props);
    this.projects = props.projects;
  }

  render() {
    return (
        <div>
          <div className="row">
            <div className="column"><ItemBox id='1' projects={this.projects}/></div>
            <div className="column"><ItemBox id='2' projects={this.projects}/></div>
            <div className="column"><ItemBox id='3' projects={this.projects}/></div>
          </div>

          <div className="row">
            <div className="column"><ItemBox id='4' projects={this.projects}/></div>
            <div className="column"><ItemBox id='5' projects={this.projects}/></div>
            <div className="column"><ItemBox id='6' projects={this.projects}/></div>
          </div>
    </div>
    );
  }
}

export default TechnicalProjects;